
import { useStore } from '@/store'
import { computed, defineComponent, ref } from 'vue'
export default defineComponent({
  props: {
    // 网络数据
    listdate: {
      type: Array,
      required: true
    },
    // 标题标签名字配置文件
    propLista: {
      type: Array,
      required: true
    },
    // 序号 v-band自动绑定
    tablexuhao: {
      type: Boolean,
      default: false
    },
    // 选中
    tablexuanze: {
      type: Boolean,
      default: false
    }
  },
  setup(props) {
    const store = useStore()
    const btn = (index: number) => {
      console.log(index)
    }
    const listdates = computed(() => props.listdate)
    const rowblclick = (event: any) => {
      console.log(event, '点击了')
      console.log(listdates.value, '点击了')
      store.dispatch('pageMusic/PlayMessage', event)
    }
    // 点击喜欢
    const like = ref()
    const likebtn = (item: any) => {
      console.log(typeof item.index, 'index')
      like.value = item.index
      console.log(item, 'index')

      store.dispatch('searchdetail/LikeMusic', {
        id: item.id,
        like: false,
        userId: store.state.login.profiles.userId
      })
    }
    return {
      likebtn,
      like,
      listdates,
      rowblclick,
      btn
    }
  }
})
